import React, { useCallback, useEffect, useState } from 'react'
import {useTranslation} from "react-i18next"
import { TRANSLATE_NS } from 'src/constants'
import useIsMountedRef from 'src/hooks/useIsMountedRef'
import { useSnackbar } from 'notistack'
import { getList } from 'src/services/borrowingHistoryService'
import Page from 'src/components/Page'
//import SplashScreen from 'src/components/SplashScreen'
import Header from './Header'
import Results from './Results'
import Paging from './Paging'
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core'

const useStyles = makeStyles((theme)=> ({
  root: {
    backgroundColor: theme.palette.background.dark,
  },
  content:{
    marginTop: theme.spacing(3)
  }
}))

function ListView(){
  const classes = useStyles();
  const {t} = useTranslation([TRANSLATE_NS.TRANSLATIONS, TRANSLATE_NS.BORROWING_HISTORY])  
  const { enqueueSnackbar } = useSnackbar()
  const sortOptions = [
    { value: 'surgicalType|asc', label: 'Surgical Type (Ascending A to Z)' },
    { value: 'surgicalType|desc', label: 'Surgical Type (Descending Z to A)' }
  ]
  const initialFilters = {
    surgicalType: '',
    status: '',
    borrowedBy: '',
    borrowTimestampFrom: '',
    borrowTimestampTo: '',
    returnedBy: '',
    returnedTimestampFrom: '',
    returnedTimestampTo: ''
  }
  const isMounted = useIsMountedRef()
  const [loading, setLoading] = useState(true)
  const [borrowTimes, setBorrowTimes] = useState(null)
  const [page, setPage] = useState(0)
  const [sort, setSort] = useState(sortOptions[0].value)
  const [limit, setLimit] = useState(8)
  const [filters, setFilters] = useState(initialFilters)

  const handleSortChange = (value) => {  
    setSort(()=> value)
    fetch(filters, value, page, limit)
  }
  const handlePageChange = (newPage) => { 
    setPage(() => newPage)
    fetch(filters, sort, newPage, limit)
  }
  const handleLimitChange = (value) => {
    setLimit(() => value)    
    fetch(filters, sort, page, value)
  }
  const handleFilters = (filters) => {
    setFilters(() => ({ ...filters }))
    fetch(filters, sort, page, limit)
  }

  const fetch = useCallback(async (filters, sort, page, limit) => {
    setLoading(true)

    try{
      const paramFilters = []
      let filterBorrowTimestamp = {}
      let filterReturnedTimestamp = {}
      Object.entries(filters).forEach((item) => {
        const field = item[0]
        const value = item[1]
        switch(field){
          case 'surgicalType':           
          case 'borrowedBy': 
          case 'returnedBy':
          {
            if(value === '' || !value) break
            else {
              paramFilters.push({ field, value, operator: 'contains' })
              break
            }
          }
          case 'status': 
          {
            if(value === '' || !value) break
            else {
              paramFilters.push({ field, value, operator: 'eq' })
              break
            }
          }
          case 'borrowTimestampFrom': 
          case 'borrowTimestampTo': 
          {
            if(value === '' || !value) break
            else {
              //const dt = dayjs(new Date(value)).unix() //.format('YYYYMMDDTHH:mmsZ')
              //console.log(dt, ' | ', dayjs.unix(dt).format('YYYYMMDD HH:mm'))
              filterBorrowTimestamp[field] = value
              break
            }
          }
          case 'returnedTimestampFrom':
          case 'returnedTimestampTo':
          {
            if(value === '' || !value) break
            else {
              filterReturnedTimestamp[field] = value
              break
            }
          }
          default: break;
        }
      })
      if(Object.keys(filterBorrowTimestamp).length){
        const borrowTimestampFrom = filterBorrowTimestamp['borrowTimestampFrom']
        const borrowTimestampTo = filterBorrowTimestamp['borrowTimestampTo']
        if(borrowTimestampFrom && borrowTimestampTo){
          paramFilters.push({
            logic: 'and', filters: [
              { field: 'borrowTimestamp', value: borrowTimestampFrom, operator: 'gte' },
              { field: 'borrowTimestamp', value: borrowTimestampTo, operator: 'lte' }
            ]
          })
        }
        if(borrowTimestampFrom && !borrowTimestampTo){
          paramFilters.push({ field: 'borrowTimestamp', value: borrowTimestampFrom, operator: 'gte' })
        }
        if(!borrowTimestampFrom && borrowTimestampTo) {
          paramFilters.push({ field: 'borrowTimestamp', value: borrowTimestampTo, operator: 'lte' })
        }
      }
      if(Object.keys(filterReturnedTimestamp).length){
        const returnedTimestampFrom = filterReturnedTimestamp['returnedTimestampFrom']
        const returnedTimestampTo = filterReturnedTimestamp['returnedTimestampTo']
        if(returnedTimestampFrom && returnedTimestampTo){
          paramFilters.push({
            logic: 'and', filters: [
              { field: 'returnedTimestamp', value: returnedTimestampFrom, operator: 'gte' },
              { field: 'returnedTimestamp', value: returnedTimestampTo, operator: 'lte' }
            ]
          })
        }
        if(returnedTimestampFrom && !returnedTimestampTo){
          paramFilters.push({ field: 'returnedTimestamp', value: returnedTimestampFrom, operator: 'gte' })
        }
        if(!returnedTimestampFrom && returnedTimestampTo) {
          paramFilters.push({ field: 'returnedTimestamp', value: returnedTimestampTo, operator: 'lte' })
        }
      }
      
      const result = await getList({filters: { logic: 'and', filters: paramFilters }, sort, page, limit})
      if(!isMounted.current)
        return;

      console.log('result: ', result)

      if(result.resultFlag){
        setBorrowTimes(result.value)
      }
      else{
        enqueueSnackbar(result.errors.message, {variant: "error"})
      }
    }
    catch(error){
      if(isMounted.current) {
        const { errorCode = "", errorMessage ="" } = error
        const msg = `${errorCode}: ${errorMessage}`
        enqueueSnackbar(msg, {variant: "error"})
      } 
    }
    finally {
      if(isMounted.current) setLoading(false)
    }
  }, [isMounted, enqueueSnackbar])

  useEffect(()=> {
    fetch({
      surgicalType: '',
      status: '',
      borrowedBy: '',
      borrowTimestampFrom: '',
      borrowTimestampTo: '',
      returnedBy: '',
      returnedTimestampFrom: '',
      returnedTimestampTo: ''
    }, "surgicalType|asc", 0, 8)

  }, [fetch])

  console.log('loading: ', loading, borrowTimes)


  if(!borrowTimes){
    //return <SplashScreen />;    
  }

  //console.log('-----------render-----------', borrowTimes)

  const displayItems = !borrowTimes || !borrowTimes.data ? [] : [...borrowTimes.data]
  const displayItemsCount = !borrowTimes || !borrowTimes.data ? 0 : borrowTimes.data.length
  const displayTotal = !borrowTimes || !borrowTimes.total ? 0 : borrowTimes.total

  return (
    <Page
      className={classes.root}
      title={t("borrowingHistory:page title")}
    >
      <Header onFilters={handleFilters} />
      <Container 
        maxWidth="lg"
        className={classes.content}
      >
        <Box>      
          <Results
            loading={loading} 
            borrowTimes={displayItems}
            itemCounts={displayItemsCount}
            onSortChange={handleSortChange}
            limit={limit} 
          />
          <Paging 
            mt={1}
            page={page} 
            limit={limit} 
            count={displayTotal}
            onChangePage={handlePageChange}
            onChangeRowsPerPage={handleLimitChange}
          />
        </Box>
      </Container>
    </Page>
  )
}

export default ListView;