import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { v4 as uuidv4 } from 'uuid'
import clsx from 'clsx'
import {
  makeStyles,
  Grid
} from '@material-ui/core'
import Sorting from './Sorting'
import BorrowTimeCard from './BorrowTimeCard'

const useStyles = makeStyles((theme) => ({
  root: {},
  sortWrapper: {
    marginBottom: theme.spacing(1)
  }
}));

function Results({ 
  loading,
  borrowTimes, 
  itemCounts,   
  limit,
  onSortChange,
  onRemove,
  className, 
  ...rest }) {

  const classes = useStyles();
  const [mode, setMode] = useState('');

  const handleModeChange = (value) => {
    setMode(value);
  }

  if(loading === true){
    var obj = []; 
    for(var index = 0, total = 8; index < total; index++) 
      obj.push({ id: uuidv4() })
    
    borrowTimes = [...obj]
  }

  return (
    <div
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Sorting 
        itemCounts={itemCounts}
        onSortChange={onSortChange} 
        onModeChange={handleModeChange}
        className={classes.sortWrapper}
      />
      <Grid
        container
        spacing={1}
      >
        {borrowTimes.map((time) => (
          <Grid
            item
            key={time.id}
            md={mode === 'grid' ? 3 : 12}
            sm={mode === 'grid' ? 6 : 12}
            xs={12}
          >
            <BorrowTimeCard borrowTime={time} loading={loading} />
          </Grid>
        ))}
      </Grid>
    </div>
  );
}

Results.propTypes = {
  loading: PropTypes.bool,
  className: PropTypes.string,
  itemCounts: PropTypes.number,
  limit: PropTypes.number,
  borrowTimes: PropTypes.array.isRequired,
  onSortChange: PropTypes.func
};

Results.defaultProps = {
  loading: true,
  itemCounts: 0,
  limit: 0,
  borrowTimes: [],
  onSortChange: () => {},
  className: ''
}

export default Results;