import React, { useState } from 'react'
import PropTypes from 'prop-types'
import clsx from 'clsx'
import {useTranslation} from "react-i18next"
import { TRANSLATE_NS, BORROWING_STATUS } from 'src/constants'
import {
  Box,
  Button,
  Card,
  Container,
  Divider,
  TextField,
  makeStyles,
  Hidden,
  IconButton, 
  CardActions, 
  Collapse, 
  CardContent, 
  Typography, 
  Grid
} from '@material-ui/core'
import { 
  Search as SearchIcon , 
  RotateLeftOutlined as ClearIcon,
  ExpandMore as ExpandMoreIcon
} from "@material-ui/icons"
import { useForm } from 'react-hook-form'
//import { KeyboardDateTimePicker } from "@material-ui/pickers"

const useStyles = makeStyles((theme)=>({
  root: {},  
  queryButton: {
    marginLeft: theme.spacing(1),
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2)
  },
  queryDivider: {
    margin: theme.spacing(0, 3),
  },
  queryCard: {
    minHeight: 60,
    borderRadius: theme.spacing(1),
    padding: theme.spacing(2),    
  },
  queryField: {
    width: '95%',
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(1)
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
}))

function Filters({onFilters, className, ...rest}){
  const classes = useStyles()
  const {t} = useTranslation([TRANSLATE_NS.SETS, TRANSLATE_NS.TRANSLATIONS])

  const statusOptions = Object.entries(BORROWING_STATUS).map((item)=> ({ label: item[0], value: item[1] }))
  const initialFilters = {
    surgicalType: '',
    status: '',
    borrowedBy: '',
    borrowTimestampFrom: '',
    borrowTimestampTo: '',
    returnedBy: '',
    returnedTimestampFrom: '',
    returnedTimestampTo: ''
  }
  const { register, reset, handleSubmit, formState, errors } = useForm({
    mode: "onChange",
    defaultValues: initialFilters
  })
  const { isDirty, isSubmitting, isSubmitted } = formState

  const [expanded, setExpanded] = useState(false)
  const handleExpandClick = () => {
    setExpanded(!expanded);
  }

  const handleClearFilters = (event) => {
    event.persist()
    reset({...initialFilters})
    if(isSubmitted === true){
      onFilters({...initialFilters})
    }
  }

  const onSubmit = (data) => {
    if(isDirty){
      onFilters({...data})
    }
  }

  const hasError = inputName => !!(errors && errors[inputName])
  const errorMessage = inputName => errors[inputName]?.message ?? null

  console.log(errors)
  return (
    <Container 
      maxWidth="md"
      className={clsx(className, classes.root)}
      {...rest}
    >
      <form onSubmit={handleSubmit(onSubmit)}>
        <Card
          className={classes.queryCard}
        >
          <Box
            display="flex"
            alignItems="center"
          >
            <TextField
              fullWidth={true}
              className={classes.queryField}    
              size="small"
              label={t('borrowingHistory:search surgical type')}
              placeholder={t('borrowingHistory:search surgical type placeholder')}
              name={"surgicalType"}
              inputRef={register}
              error={hasError("surgicalType")}
              helperText={errorMessage("surgicalType")}
            />
            <Hidden only={["md", "lg", "xl"]}>
              <IconButton      
                color="primary"             
                disabled={isSubmitting}
                onClick={handleClearFilters}
                size="small"
              >
                <ClearIcon />
              </IconButton>
              <IconButton 
                type="submit"
                color="primary"
                disabled={isSubmitting}   
                size="small"             
              >
                <SearchIcon />
              </IconButton>
            </Hidden>
            <Hidden smDown>
              <Divider
                orientation="vertical"
                flexItem
                className={classes.queryDivider}
              />
              <Button
                variant="outlined"
                color="secondary"
                className={classes.queryButton}
                disabled={isSubmitting}
                onClick={handleClearFilters}
                size="small"
              >
                <ClearIcon />
                {t('translations:clear')}
              </Button>
              <Button
                type="submit"
                variant="outlined"
                color="primary"
                className={classes.queryButton}
                disabled={isSubmitting}
                size="small"
              >
                <SearchIcon />
                {t('translations:search')}
              </Button>
            </Hidden>
            <CardActions disableSpacing>
              <IconButton 
                className={clsx(classes.expand, {
                  [classes.expandOpen]: expanded,
                })}
                aria-label="Advance filters"
                aria-expanded={expanded}
                onClick={handleExpandClick}
                title={t('translations:advance filters')}
                size="small"
              >
                <ExpandMoreIcon />
              </IconButton>
            </CardActions>
          </Box>          
          <Collapse in={expanded} timeout="auto" unmountOnExit >
            <CardContent>
              <Typography variant="h4">
                {t('translations:advance filters')}
              </Typography>
              <Grid container spacing={2} style={{marginTop: 8}}>
                <Grid item xs={12} md={8}>
                  <TextField 
                    fullWidth
                    name={"status"}
                    inputRef={register}
                    variant="outlined"
                    label={t('borrowingHistory:status')}
                    select
                    SelectProps={{native: true}}
                    InputLabelProps={{ shrink: true }}
                    size="small"
                    error={hasError("status")}
                    helperText={errorMessage("status")}
                  >
                    {statusOptions.map((option)=>(
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid 
                  item 
                  container 
                  xs={12} 
                  spacing={2}
                  direction="row"
                  justify="flex-start"
                  alignItems="center"
                >
                  <Grid item xs={12} sm={12} md={6} lg={4}>
                    <TextField
                      fullWidth
                      inputRef={register}
                      name={"borrowedBy"}
                      label={t('borrowingHistory:borrowed by')}
                      InputLabelProps={{ shrink: true }}
                      size="small"
                      variant="outlined"
                    />
                  </Grid>
                  <Grid item xs={12} sm={12} md={6} lg={4}>
                    <TextField
                      fullWidth
                      inputRef={register}
                      name={"borrowTimestampFrom"}
                      type="datetime-local"
                      label={t('borrowingHistory:borrowed timestamp from')}
                      InputLabelProps={{ shrink: true }}
                      size="small"
                      variant="outlined"
                    />
                  </Grid>
                  <Grid item xs={12} sm={12} md={6} lg={4}>
                    <TextField 
                      fullWidth
                      inputRef={register}
                      name={"borrowTimestampTo"}
                      type="datetime-local"
                      label={t('borrowingHistory:borrowed timestamp to')}
                      InputLabelProps={{ shrink: true }}
                      size="small"
                      variant="outlined"
                    />
                  </Grid>
                </Grid>
                <Grid 
                  item 
                  container 
                  xs={12} 
                  spacing={2}
                  direction="row"
                  justify="flex-start"
                  alignItems="center"
                >
                  <Grid item xs={12} sm={12} md={6} lg={4}>                    
                    <TextField
                      fullWidth
                      inputRef={register}
                      name={"returnedBy"}
                      label={t('borrowingHistory:returned by')}
                      InputLabelProps={{ shrink: true }}
                      size="small"
                      variant="outlined"
                      error={hasError("returnedBy")}
                      helperText={errorMessage("returnedBy")}
                    />
                  </Grid>
                  <Grid item xs={12} sm={12} md={6} lg={4}>
                    <TextField 
                      fullWidth
                      inputRef={register}
                      name={"returnedTimestampFrom"}
                      type="datetime-local"
                      label={t('borrowingHistory:returned timestamp from')}
                      InputLabelProps={{ shrink: true }}
                      size="small"
                      variant="outlined"
                      error={hasError("returnedTimestampFrom")}
                      helperText={errorMessage("returnedTimestampFrom")}         
                    />
                  </Grid>
                  <Grid item xs={12} sm={12} md={6} lg={4}>
                    <TextField 
                      fullWidth
                      inputRef={register}
                      name={"returnedTimestampTo"}
                      type="datetime-local"
                      label={t('borrowingHistory:returned timestamp to')}
                      InputLabelProps={{ shrink: true }}
                      size="small"
                      variant="outlined"
                      error={hasError("returnedTimestampTo")}
                      helperText={errorMessage("returnedTimestampTo")}    
                    />
                  </Grid>
                </Grid>         
              </Grid>
            </CardContent>
          </Collapse>
        </Card>
      </form>
    </Container>
  )
}

Filters.propTypes = {
  className: PropTypes.string,
  onFilters: PropTypes.func
}

export default Filters