import React from 'react'
import PropTypes from 'prop-types'
import clsx from 'clsx'
import {
  Grid,
  makeStyles,
  Container,
} from '@material-ui/core'
import {THEMES, TRANSLATE_NS} from 'src/constants'
import { useTranslation } from 'react-i18next'
import BackToMainPageButton from 'src/components/BackToMainPageButton'
import PageName from 'src/components/PageName'
import Filters from './Filters'

const useStyles = makeStyles((theme) => ({
  root: {
    minWidth: '100%',
  },
  previousButton: {
    "&:hover": {
      textDecoration: "underline",
    },
  },
  titleRow: {
    paddingTop: theme.spacing(1)
  },
  headerBackground: {    
    height: 130,
    zIndex: theme.zIndex.drawer + 100,
    marginTop: theme.spacing(1),
    ...theme.name === THEMES.LIGHT ? {
      boxShadow: 'none',
      background: `linear-gradient(90deg, ${theme.palette.primary.main} 30%, ${theme.palette.secondary.main} 180%)`
    } : {},
    ...theme.name === THEMES.ONE_DARK ? {
      backgroundColor: theme.palette.background.default 
    } : {}
  },
  filterWrapper: {
    marginTop: -127
  }
}));

function Header({ onFilters, className, ...rest }) {
  const classes = useStyles();
  const {t} = useTranslation([TRANSLATE_NS.TRANSLATIONS, TRANSLATE_NS.SETS]);

  return (
    <div 
      className={clsx(classes.root, className)}
      {...rest}
    >
      <div className={classes.headerBackground}></div>
      <Container 
        maxWidth="md"
        className={classes.filterWrapper}
      >
        <Grid
          alignItems="center"
          container
          justify="space-between"
          className={classes.titleRow}
        >
          <Grid item>
            <PageName>
              {t('borrowingHistory:page title')}
            </PageName>
          </Grid>
          <Grid item>
            <BackToMainPageButton className={classes.previousButton} />
          </Grid>
        </Grid>        
        <Filters 
          onFilters={onFilters}
        />
      </Container>
    </div>
  )
}

Header.propTypes = {
  className: PropTypes.string,
  onFilters: PropTypes.func
}

export default Header;