import React from 'react'
import { useHistory, Link as RouterLink } from 'react-router-dom'
import PropTypes from 'prop-types'
import clsx from 'clsx'
import * as dayjs from 'dayjs'
import {
  Box,
  Card,
  Button,
  Grid,
  Typography,
  makeStyles,
  CardHeader,
  Link
} from '@material-ui/core'
import { useTranslation } from 'react-i18next'
import { TRANSLATE_NS } from 'src/constants'
import { Skeleton } from '@material-ui/lab'
//import { Edit2 as EditIcon } from 'react-feather'
//import MoreButton from 'src/components/MoreButton'
import BorrowingStatusLabel from 'src/components/BorrowingStatusLabel'
import ItemLayout from './ItemLayout'


const useStyles = makeStyles((theme) => ({
  root: {
    minWidth: 178
  },
  status: {
    paddingRight: theme.spacing(1)
  },
  value: {
    fontFamily: theme.typography.body2.fontFamily,
    fontWeight: theme.typography.h3.fontWeight,
  }
}))

function BorrowingCard({ loading, borrowTime: dataItem, className, ...rest }) {
  const classes = useStyles()
  const {t} = useTranslation([TRANSLATE_NS.TRANSLATIONS, TRANSLATE_NS.BORROWING_HISTORY])
  const history = useHistory()

  // const settingButtons = [
  // {
  //   translateKey: "edit",
  //   click: () => {
      
  //   },
  //   icon: <EditIcon />
  // },
  // ]

  const handleClickViewDetailsButton = (event) => {
    event.persist()
    const url = `/borrowing-history/${dataItem.id}/detail`
    history.push(url)
  }

  const displaySurgicalType = dataItem?.surgicalType ?? ""
  const displayMedicalToolsCount = dataItem?.medicalToolsCount ?? 0
  const displayMedicalItemsCount = dataItem?.medicalItemsCount ?? 0
  const borrowTimestamp = dataItem?.borrowTimestamp ? dayjs(dataItem.borrowTimestamp).format("DD-MM-YYYY HH:mm") : "" ?? ""
  const borrowBy = dataItem?.borrowBy ?? ""
  const returnTimestamp = dataItem?.returnTimestamp ? dayjs(dataItem.returnTimestamp).format("DD-MM-YYYY HH:mm"): "" ?? ""
  const returnBy = dataItem?.returnBy ?? ""
  const displayStatus = dataItem?.status ?? ""

  const displayReturnedBy = returnBy ? `${returnBy} | ${returnTimestamp}`: "-"
  const displayBorrowedBy = borrowBy ? `${borrowBy} | ${borrowTimestamp}`: "-"

  const displayItems = [
    { label: t('borrowingHistory:medical tools count'), value: displayMedicalToolsCount },
    { label: t('borrowingHistory:medical items count'), value: displayMedicalItemsCount },
    { label: t('borrowingHistory:borrowed by'), value: `${displayBorrowedBy}` },
    { label: t('borrowingHistory:last returned by'), value: `${displayReturnedBy}` }
  ]

  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <CardHeader 
        title={
          loading ? (
            <Skeleton animation="wave" variant="text" data-testid="skeleton-title"></Skeleton>
          ) : (
            <Grid container direction="row" alignItems="center">
              <Grid item className={classes.status}>
                <BorrowingStatusLabel status={displayStatus}  />
              </Grid>
              <Grid item>                
                <Link
                  className={classes.name}
                  color="inherit"
                  component={RouterLink}
                  to={`/borrowing-history/${dataItem.id}/detail`}
                  underline="none"
                >
                  <Typography
                    color="textPrimary"
                    variant="h3"                  
                  >
                    {displaySurgicalType}
                  </Typography>
                </Link>
              </Grid>
            </Grid>            
          )
        }
        // action={
        //   loading ? null : (<MoreButton buttonList={settingButtons} />)
        // }
      />
      <Box
        pb={1}
        px={3}
      >
        {loading ? (
          <div data-testid="skeleton-details">
            <Skeleton animation="wave" height={15} style={{marginBottom: 6}} />
            <Skeleton animation="wave" height={10} width="80%" style={{marginBottom: 6}} />
            <Skeleton animation="wave" height={10} width="80%" style={{marginBottom: 6}} />
            <Skeleton animation="wave" height={10} width="90%" />
          </div>
        ) : (
          <Grid 
            container
            alignItems="center"
            justify="space-between"
            spacing={1}
          >
            {displayItems.map((item) => {
              const { label, value } = item
              return (
                <ItemLayout key={label}>
                  <Grid item>
                    <Typography gutterBottom variant="body2" color="textSecondary" >
                      {label}
                    </Typography>
                  </Grid>
                  <Grid item>
                    <Typography className={classes.value}>
                      {value}
                    </Typography>
                  </Grid>
                </ItemLayout>
              )
            })} 
          </Grid>
        )}
      </Box>
      <Box
        py={2}
        px={2}
      >
        <Grid
          alignItems="center"
          container
          justify="flex-end"
          spacing={2}
        >
          <Grid item>
            {loading ? (
              <div data-testid="skeleton-view-details-button">
                <Skeleton animation="wave" height={10} width="80%" />
              </div>
              ) : (
              <>
              <Button
                color="secondary"
                onClick={handleClickViewDetailsButton}
                variant="outlined"
                size="small"
              >
                {t('borrowingHistory:view details')}
              </Button>              
              </>
            )}
          </Grid>
        </Grid>
      </Box>
    </Card>
  )
}

BorrowingCard.propTypes = {
  className: PropTypes.string,
  borrowTime: PropTypes.shape({
    id: PropTypes.string,
    surgicalType: PropTypes.string,
    medicalToolsCount: PropTypes.number,
    medicalItemsCount: PropTypes.number,
    borrowTimestamp: PropTypes.number,
    borrowBy: PropTypes.string,
    returnTimestamp: PropTypes.number,
    returnBy: PropTypes.string,
    status: PropTypes.string,
    // createdTimestamp: PropTypes.number,
    // createdBy: PropTypes.string,
  }),
  loading: PropTypes.bool,
  onRemove: PropTypes.func
}

export default BorrowingCard